import jsPDF from "jspdf";
import "jspdf-autotable";

declare module "jspdf" {
  interface jsPDF {
    autoTable: (...args: any[]) => jsPDF;
  }
}

export const exportToPDF = (data: any[]) => {
  const doc = new jsPDF();
  doc.text("Expense Report", 14, 10);
  doc.autoTable({
    head: [["Title", "Amount", "Category", "Date"]],
    body: data.map((e) => [e.title, e.amount, e.category, e.date]),
  });
  doc.save("expenses.pdf");
};
